
@font-face {
    font-family: "m5x7";
    src: url(/about-me/static/media/m5x7.1c36fac5.ttf),
         url(/about-me/static/media/m5x7.b99dd26c.woff),
         url(/about-me/static/media/m5x7.1681d9b9.woff2),
         url(/about-me/static/media/m5x7.dc97bf00.svg#filename);
}

@font-face {
    font-family: "m6x11";
    src: url(/about-me/static/media/m6x11.cc8dfaa3.ttf),
         url(/about-me/static/media/m6x11.06395abf.woff),
         url(/about-me/static/media/m6x11.4b67f4ca.woff2),
         url(/about-me/static/media/m6x11.ec0d4e88.svg#filename);
}

body {
    background-color: black;
    position: absolute;
    height: 99%;
    width: 99%;
}

a {
    text-decoration: none;
    color: rgb(124, 255, 179);
}

.logo {
    width: 128px;
    height: 128px;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;
}
  
.header-text {
    font-family: "m6x11";
    font-size: 48px;
    font-weight: normal;
    font-style: normal;

    color: rgb(255, 255, 255);
}

.subheader-text {
    font-family: "m5x7";
    font-size: 36px;
    font-weight: normal;
    font-style: normal;
    
    color: rgb(199, 199, 199);
}

.about-text {
    font-family: 'm5x7';
    font-size: 24px;
    font-weight: normal;
    font-style: normal;
    
    color: rgb(225, 225, 225);
}

.center-align {
    text-align: center;
}

.work-box {
    background-color: rgba(0, 0, 0, 0);

    border: 16px solid transparent;
    -webkit-border-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACIAAAAiCAQAAACQTsNJAAAAdUlEQVRIie2WsRGAMAwDnxyzhoqFGCCrZI0swAyiIBBqQYflwudCb6szfKCpd73xpxNRkFHlsVxFrorQeY7cLD3PlF74bwUkIAEJSEB+A6m2vQ5IMzGVBsAMsJt3tO5MwLKamJ0VlmvK1lMgRIbxn2Qz0QZwAOv8hkbbHFLZAAAAAElFTkSuQmCC) 7.5 repeat;
            border-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACIAAAAiCAQAAACQTsNJAAAAdUlEQVRIie2WsRGAMAwDnxyzhoqFGCCrZI0swAyiIBBqQYflwudCb6szfKCpd73xpxNRkFHlsVxFrorQeY7cLD3PlF74bwUkIAEJSEB+A6m2vQ5IMzGVBsAMsJt3tO5MwLKamJ0VlmvK1lMgRIbxn2Qz0QZwAOv8hkbbHFLZAAAAAElFTkSuQmCC) 7.5 fill repeat;
    box-sizing: border-box;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;

    -webkit-transition: all 600ms cubic-bezier(0.215, 0.61, 0.355, 1);
    transition:         all 600ms cubic-bezier(0.215, 0.61, 0.355, 1);

    z-index: 0;

    max-width: 240px;
}

.work-box:hover {
    z-index: 1;
    -webkit-transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition:         all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    cursor: pointer;
}

.work-img-container {
    position: relative;
}

.work-gif-note {
    position: absolute;
    top: 4px;
    left: 5px;
    z-index: 3;

    font-family: "Roboto";
    font-size: 12px;
    letter-spacing: 0.5px;

    background-color: #00000094;

    padding: 2px 2px 2px 2px;
    border-radius: 3px;

    color: rgb(219, 219, 219);
}

.work-img {
    position: absolute;
    z-index: 2;

    border: 2px solid #000000;
    padding: 2px;
    border-radius: 5px;

    opacity: 1;
    -webkit-transition: 0.3s;
    transition: 0.3s;

    width: 200px;
}

.work-img:hover {
    border: 2px solid #000000;
    padding: 2px;
    border-radius: 5px;

    opacity: 0;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.work-gif {
    position: relative;

    border: 2px solid #000000;
    padding: 2px;
    border-radius: 5px;

    width: 200px;
}

.work-title {
    font-family: "Roboto";
    font-size: 15px;
    font-weight: bold;
    font-style: normal;
    
    text-align: center;

    color: rgb(0, 0, 0);
}

.work-job {
    font-family: "Roboto";
    font-size: 13px;
    font-weight: bold;
    font-style: normal;
    
    text-align: center;
    padding-top: 5px;
    
    color: rgb(27, 27, 27);
}

.work-description {
    font-family: "m5x7";
    font-size: 16px;
    font-weight: normal;
    font-style: normal;

    text-align: center;
    padding-top: 12px;
    
    color: rgb(27, 27, 27);
}

.work-team {
    font-family: "m5x7";
    font-size: 16px;
    font-weight: normal;
    font-style: normal;

    text-align: left;
    padding-top: 12px;
    
    color: rgb(27, 27, 27);
}

.team-icon {
    width: 13px;
    height: 13px;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;
}
